/* @flow */

import { getClientID, getMerchantID, getLogger } from "@paypal/sdk-client/src";
import { stringifyError } from '@krakenjs/belter/src';

import type { ApplePayPaymentContact } from "./types";

type ApplepayConfig = {|
  isApplePayEligible : boolean,
  countryCode : string,
  currencyCode : string,
  merchantCapabilities : $ReadOnlyArray<string>,
  supportedNetworks : $ReadOnlyArray<string>
|};

type ValidateMerchanParms = {|
    url : string,
    clientID : string,
    orderID : string,
    merchantDomain : string
|};

const DEFAULT_HEADERS =  {
  "Content-Type": "application/json",
  Accept: "application/json",
}

async function createOrder(payload) {
  const clientID = getClientID();
  const basicAuth = btoa(`${clientID}:`);

  const accessToken = await fetch(
    //   "https://api.sandbox.paypal.com/v1/oauth2/token",
    "/v1/oauth2/token",
    {
      method: "POST",
      headers: {
        Authorization: `Basic ${basicAuth}`,
      },
      body: "grant_type=client_credentials",
    }
  )
    .then((res) => res.json())
    .then((res) => {
      return res.access_token;
    });

  // "https://api.sandbox.paypal.com/v2/checkout/orders"
  const res = await fetch("/v2/checkout/orders", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${accessToken}`,
    },
    body: JSON.stringify(payload),
  });
  const { id } = await res.json();

  return {
    id,
  };
}

async function config() {
  const clientID = getClientID();

  return await fetch(
    //  "https://cors-anywhere.herokuapp.com/https://www.sandbox.paypal.com/graphql?GetApplepayConfig",
    "/graphql?GetApplepayConfig",
    {
      method: "POST",
      headers: {
        ...DEFAULT_HEADERS
      },
      body: JSON.stringify({
        query: `
          query GetApplepayConfig(
            $buyerCountry: CountryCodes!
            $clientId: String!
            $merchantId: [String]!
          ) {
            applepayConfig(
              buyerCountry: $buyerCountry
              clientId: $clientId
              merchantId: $merchantId
            ) {
              merchantCountry,
              supportedNetworks
            }
          }`,
        variables: {
          buyerCountry: "US",
          clientId: clientID,
          merchantId: ["2V9L63AM2BYKC"],
        },
      }),
    }
  )
    .then((res) => res.json())
    .then(({ data, errors }) => {
      if(Array.isArray(errors) && errors.length){
        throw new Error(errors[0].message)
      }
      return data;
    })
    .then(data => data.applepayConfig)
    .catch(console.error);
}

let orderID;

async function validateMerchant({ validationUrl }) {
  const clientID = getClientID();

  const { id } = await createOrder({
    intent: "CAPTURE",
    purchase_units: [
      {
        amount: {
          currency_code: "USD",
          value: "1.00",
        },
        payee: {
          merchant_id: "2V9L63AM2BYKC",
        },
      },
    ],
  });

  orderID = id;

  return await fetch(
    // "https://cors-anywhere.herokuapp.com/https://www.sandbox.paypal.com/graphql?GetApplepayConfig",v
    "/graphql?GetApplepayConfig",
    {
      method: "POST",
      headers: {
        ...DEFAULT_HEADERS
      },
      body: JSON.stringify({
        query: `
            query GetApplePayMerchantSession(
                $url : String!
                $orderID : String!
                $clientID : String!
                $merchantDomain : String!
            ) {
                applePayMerchantSession(
                    url: $url
                    orderID: $orderID
                    clientID: $clientID
                    merchantDomain: $merchantDomain
                ) {
                    session
                }
            }`,
        variables: {
          url: validationUrl,
          clientID: clientID,
          orderID: id,
          merchantDomain: "sandbox-applepay-paypal-js-sdk.herokuapp.com",
        },
      }),
    }
  )
    .then(res => {
      if(!res.ok) {
        throw new Error("GetApplePayMerchantSession response error")
      }
      return res
    })
    .then((res) => res.json())
    .then(({ data, errors }) => {
      if(Array.isArray(errors) && errors.length){
        const message = errors[0].message || JSON.stringify(errors[0]);
        throw new Error(message)
      }
      return data;
    })
    .then((data) => data.applePayMerchantSession)
    .then((merchantSession) => {
      const payload = atob(merchantSession.session);
      return JSON.parse(payload);
    })
    .catch((err) => {
      getLogger().error('GetApplepayConfig_error', { err: stringifyError(err) });
      throw err
    });
}

async function approvePayment({ orderID, payment }) {
  const clientID = getClientID();

  return await fetch(
    "/graphql?ApproveApplePayPayment",
   //  "https://cors-anywhere.herokuapp.com/https://www.sandbox.paypal.com/graphql?ApproveApplePayPayment",
    {
      method: "POST",
      headers: {
        ...DEFAULT_HEADERS
      },
      body: JSON.stringify({
        query: `
        mutation ApproveApplePayPayment(
          $token: ApplePayPaymentToken!
          $orderID: String!
           $clientID : String!
           $billingContact: ApplePayPaymentContact!
           $shippingContact: ApplePayPaymentContact
        ) {
          approveApplePayPayment(
            token: $token
            orderID: $orderID
            clientID: $clientID
            billingContact: $billingContact
            shippingContact: $shippingContact
          )
        }`,
        variables: {
          token: payment.token,
          billingContact: payment.billingContact,
          shippingContact: payment.shippingContact,
          clientID: clientID,
          orderID,
        },
      }),
    }
  )
  .then(res => {
    if(!res.ok) {
      throw new Error("ApproveApplePayPayment response error")
    }
    return res
  })
    .then((res) => res.json())
    .then(({ data, errors }) => {
      if(Array.isArray(errors) && errors.length){
        const message = errors[0].message || JSON.stringify(errors[0]);
        throw new Error(message)
      }
      return data;
    })
    .catch((err) => {
      getLogger().error('ApproveApplePayPayment_error', { err: stringifyError(err) });
      throw err
    });
}


export function Applepay() : Object {

    return {
      createOrder,
      config,
      validateMerchant,
      approvePayment,
    }
}
